@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;700&display=swap);
.light{--theme-color: #a80000;--theme-page-background: #ddc;--theme-page-text: #111}.dark{--theme-color: #0000a8;--theme-page-background: #111;--theme-page-text: #444}

body{margin:0}code{font-family:source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace}.App{height:100vh;background-color:#ededed;display:flex;flex-direction:column;justify-content:center;align-items:center;font-family:"Roboto", sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;color:var(--theme-page-text)}.auth-form-body{border-top:2px solid #06874e;width:350px;background-color:#fff;padding:25px}@media screen and (max-width: 400px){.auth-form-body{width:auto}}.auth-form-body .title{text-align:center;font-size:29px;font-weight:bold;margin-bottom:10px}.auth-form-body .button-submit{height:45px;width:100%;display:flex;justify-content:center;align-items:center;background-color:#06874e;border:0;color:#fff;font-size:20px;cursor:pointer;transition:all 0.3s}.auth-form-body .button-submit img{height:25px}.auth-form-body .button-submit:hover{opacity:0.8}.auth-form-box-error{padding:5px;box-sizing:border-box;text-align:center;width:100%;margin-top:5px;margin-bottom:5px;background-color:#f77;font-size:14px}.auth-form-input label{font-size:18px;font-weight:bold}.auth-form-input input{font-size:16px;width:100%;height:45px;margin-bottom:15px;box-sizing:border-box;border:1px solid #ddd;padding:10px 5px}.auth-form-remember{display:flex;align-content:center;width:"auto";position:relative;padding-left:25px;margin-bottom:15px;border:1px solid #fff;cursor:pointer;font-size:18px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.auth-form-remember input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.checkmark{position:absolute;top:2px;left:0;height:15px;width:15px;background-color:#fff;border:1px solid #444;transition:all 0.3s}.auth-form-remember:hover input ~ .checkmark{background-color:#ccc}.auth-form-remember input:checked ~ .checkmark{background-color:#06874e}.auth-form-remember input:focus ~ .checkmark{box-shadow:0 0 10px #aaa}.checkmark:after{content:"";position:absolute;display:none}.auth-form-remember input:checked ~ .checkmark:after{display:block}.auth-form-remember .checkmark:after{left:5px;top:2px;width:4px;height:7px;border:solid white;border-width:0 2px 2px 0;transform:rotate(45deg)}.auth-form-footer{display:flex;align-items:center;flex-direction:column;margin-top:20px;font-size:14px}.auth-form-footer div{text-align:center;margin-bottom:10px;width:auto}.auth-form-footer a{font-weight:bold;color:#000;text-decoration:underline;cursor:pointer}

